.memoTab-box{
  position: absolute;
  top: 40%;
  left: 15%;
  transform: translate(-50%, -50%);

  width: 200px;
  height: 500px;
  background-color: transparent;
}

.memoTab-add-btn{
  width: 50px;
  display: block;
  margin: 10px auto;
  text-align: center;
}

.buttons{
  position: absolute;
  top: 85%;
  left: 60%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: 100px;
}

.buttons > button{
  margin-left: 10px;
  margin-top: 10px;
  animation-name: slowfade;
  animation-duration: 2.0s;
}

@keyframes slowfade{
  0%{
    opacity: 0;
  }
  50%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes fade{
  0%{
    opacity: 0;
    width: 0px;
  }
  100%{
    opacity: 1;
  }
}